// @flow
import React from 'react';

import PageTitle from '../../../components/PageTitle';

const Regions = () => {
    return (
        <React.Fragment>
            <PageTitle
                breadCrumbItems={[{ label: 'Administración' }, { label: 'Catálogos' }, { label: 'Región', active: true }]}
                title={'Divisiones / Regiones'}
            />
        </React.Fragment>
    );
};

export default Regions;